.catagory_section .btn-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  margin-top: 45px;
}

.catagory_section .btn-box a {
  display: inline-block;
  padding: 10px 45px;
  background-color: #44b89d;
  color: #ffffff;
  border-radius: 5px;
  border: 1px solid #44b89d;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.click-box {
  display: inline-block;
  padding: 10px 45px;
  background-color: #44b89d;
  color: #ffffff;
  border-radius: 5px;
  border: 3px solid #44b89d;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  font-size: 24px;
  font-weight: bold;
}

.click-box:hover {
  background-color: transparent;
  color: #44b89d;
}

.page-container {
  display: flex;
  height: 100vh;
  width: 100vw;
}

.fixed-section {
  width: 60%;
  background-color: #fff;
